.testimonials-gallery{
  padding-top: 155px;
  @include lg{
    padding-top: 60px;
    .section-header{
      margin-bottom: 45px;
    }
  }
  &-slider{
    &.swiper-initialized{
      .swiper-slide{
        a{
          display: block;
        }
        @media screen and (min-width: 576px) {
          a {
            position: relative;
            padding-top: 100%;
          }
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            border: 1px solid #ddd;
          }
        }
        @include sm{
          padding: 0;
          img{
            max-width: 100%;
            width: 100%;
          }
        }
      }
    }
  }
}