:root{
  --body-font               : 'Raleway', Arial, Helvetica, sans-serif;
  --body-line-height        : 135%;
  --body-text-color         : #000;
  --body-bg-color           : #fff;

  --heading-color           : #202020;
  --heading-line-height     : 112.2%;
  --heading-font-weight     : 700;
  --heading-font            : 'Montserrat', Arial, Helvetica, sans-serif;
  --heading-margin          : 0 0 25px 0;
  --h1-size                 : 60px;
  --h2-size                 : 40px;
  --h3-size                 : 36px;
  --h4-size                 : 32px;
  --h5-size                 : 26px;
  --h6-size                 : 24px;

  --red                     : #C80303;
  --black                   : #141414;

  --button-bg               : #2D308F;
  --button-color            : #fff;

  --button-white-bg         : #fff;
  --button-white-color      : #121323;

  --section-padding         : 50px 0;

}
