.flex{
  display: flex;
  flex-wrap: wrap;
  &-center{
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.reset-list{
  margin: 0;
  padding: 0;
  list-style-type: none;
}


.hide-xl{
  @include xl{
    display: none!important;
  }
}
.hide-lg{
  @include lg{
    display: none!important;
  }
}
.hide-md{
  @include md{
    display: none!important;
  }
}
.hide-sm{
  @include sm{
    display: none!important;
  }
}

.show-xl{
  display: none!important;
  @include xl{
    display: block!important;
  }
}
.show-lg{
  display: none!important;
  @include lg{
    display: block!important;
  }
}
.show-md{
  display: none!important;
  @include md{
    display: block!important;
  }
}
.show-sm{
  display: none!important;
  @include sm{
    display: block!important;
  }
}