.cta{
  padding: 30px 0;
  @include sm{
    padding: 39px 0;
  }
  &-container{
    align-items: center;
    justify-content: space-between;
    @include xl{
      flex-direction: column;
    }
  }
  &-title{
    font-size: 20px;
    line-height: 23px;
    @include xxl{
      font-size: 18px;
    }
    @include xl{
      margin-bottom: 20px;
      text-align: center;
    }
    span{
      text-transform: uppercase;
      color: #FF6565;
    }
  }
  .button{
    @include sm{
      padding: 14px 35px;
    }
  }
}