.team{
  @include xl{
    padding: 60px 0!important;
  }
  &-row{
    &-reverse{
      @media screen and (min-width: 991px){
        flex-direction: row-reverse;
      }
    }
    @include lg{
      flex-direction: column;
    }
  }
  &-img{
    &-1{
      max-width: 717px;
      margin: -68px -50px 0 -85px;
    }
    &-2{
      max-width: 717px;
      margin: -3px -79px 0 0;
    }
    @include xl{
      .team-img-1,
      .team-img-2{
        max-width: 500px;
        margin: 0;
      }
    }
    @include lg{
      .team-img-1,
      .team-img-2{
        max-width: 100%;
        margin: -15% 0;
      }
    }
    @include sm{
      .team-img-1,
      .team-img-2{
        max-width: 146%;
        margin: -33% 0 -12px 0;
        position: relative;
        left: 50%;
        transform: translate(-50%, 0);
      }
    }
  }
  &-info{
    flex: 1;
    padding-top: 57px;
    @include sm{
      padding-top: 0;
    }
    &-header{
      margin-bottom: 38px;
      @include sm{
        text-align: center;
      }
      &-name{
        font-weight: 600;
        font-size: 40px;
        line-height: 1.3;
        text-transform: uppercase;
        @include md{
          font-size: 36px;
        }
        @include sm{
          font-size: 30px;
        }
      }
      &-position{
        font-size: 24px;
        line-height: 28px;
        @include md{
          font-size: 20px;
        }
      }
    }
    &-info{
      margin-bottom: 60px;
      table{
        @include sm{
          display: block;
        }
        tr{
          @include sm{
            display: block;
          }
          td{
            padding: 10px 0;
            font-size: 16px;
            line-height: 19px;

            &:nth-child(1){
              font-weight: 700;
              font-size: 20px;
              width: 130px;
            }
            @include xl{
              padding: 5px 0;
            }
            @include sm{
              display: block;
              font-size: 14px;
            }
          }
        }
      }
    }
    &-desc{
      &-title{
        font-weight: 600;
        font-size: 20px;
        margin-bottom: 14px;
      }
      p{
        font-size: 18px;
        line-height: 23px;
        @include xxl{
          font-size: 16px;
        }
        @include xl{
          font-size: 16px;
        }
      }
    }
  }
  &.section-black-bg{
    padding-bottom: 64px;
  }
  @media screen and (min-width: 1200px){
    &:not(.section-black-bg){
      padding-bottom: 80px;
      .team-info{
        padding-top: 90px;
      }
      .team-info-info{
        margin-bottom: 24px;
      }
    }
  }
  @include sm{
    &.section-black-bg{
      padding-top: 78px !important;
      padding-bottom: 72px!important;
    }
    &:not(.section-black-bg){
      padding-top: 33px!important;
      .team-info-info{
        margin-bottom: 37px;
      }
    }
  }
}