.modal{
  &-close{
    position: absolute;
    width: 30px;
    height: 30px;
    z-index: 2;
    opacity: 1;
    cursor: pointer;
    top: 13px;
    right: 13px;
    transition: all ease-in-out 0.2s;
    &:hover{
      transition: all ease-in-out 0.2s;
      opacity: 0.8;
    }
  }
  &-dialog{
    max-width: 1030px;
  }
  &-content{
    position: relative;
    padding: 35px 45px;
    border-radius: 0;
    box-shadow: none;
    border: none;
    @include sm{
      padding: 20px 45px 20px 20px;
    }
  }

  &-info{
    &-header{
      font-weight: 500;
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 40px;
      @include sm{
        font-size: 18px;
        line-height: 21px;
      }
    }
    &-content{
      margin-bottom: 40px;
      p{
        font-weight: 500;
        font-size: 20px;
        line-height: 23px;
        @include sm{
          font-size: 18px;
        }
      }
    }
    &-footer{
      width: calc(100% + 90px);
      position: relative;
      left: -45px;
      color: #fff;
      padding: 25px 45px;
      background: linear-gradient(89.74deg, #CA2F2F 3.02%, #EE3434 110.47%);
      @include sm{
        width: calc(100% + 66px);
        padding: 20px;
        left: -20px;
      }
      span{
        font-weight: 500;
        font-size: 20px;
        line-height: 23px;
        display: block;
        margin-bottom: 15px;
        @include sm{
          font-size: 16px;
        }
      }
    }

    &-list{
      li{
        position: relative;
        font-size: 18px;
        line-height: 14px;
        margin-bottom: 10px;
        padding-left: 30px;
        &:before{
          content: "";
          width: 23px;
          height: 23px;
          background-image: url("../images/svg/how-star.svg");
          background-size: 20px;
          background-position: center;
          background-repeat: no-repeat;
          position: absolute;
          left: 0;
          top: 50%;
          transform: translate(0, -50%);
          transition: all ease-in-out 0.2s;
        }
      }
    }

  }
}