.about{
  @include sm{
    padding-bottom: 20px;
  }
  &-header{
    position: relative;
    margin-bottom: 100px;
    @include md{
      margin-bottom: 50px;
    }
    &:after{
      content: "";
      width: 558px;
      height: 200px;
      background-image: url("../images/svg/about-decor.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      position: absolute;
      right: -30px;
      top: -51px;
      @include xl{
        width: 310px;
        height: 120px;
        top: 0;
      }
      @include md{
        width: 201px;
        height: 66px;
      }
      @include sm{
        width: 168px;
        height: 58px;
      }
    }
    h2{
      font-weight: 600;
      font-size: 60px;
      position: relative;
      letter-spacing: -3px;
      margin-bottom: 10px;
      @include xl{
        font-size: 46px;
      }
      @include lg{
        font-size: 40px;
        padding-top: 18px;
      }
      @include md{
        font-size: 36px;
      }
      &:before{
        content: "";
        width: 25px;
        height: 25px;
        background-image: url("../images/svg/about-star.svg");
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        left: -28px;
        top: -16px;
        @include lg{
          left: -5px;
          top: -7px;
        }
      }
    }
    p{
      max-width: 437px;
      color: #000;
      font-weight: 500;
      font-size: 30px;
      line-height: 1.1;
      @include xl{
        font-size: 24px;
      }
      @include md{
        font-size: 20px;
      }
    }
  }
  &-box{
    display: flex;
    align-items: center;
    @include sm{
      margin-bottom: 32px;
    }
    &-icon{
      width: 68px;
      min-width: 68px;
      margin-right: 24px;

    }
    &-text{
      flex: 1;
      font-weight: 500;
      font-size: 24px;
      padding-right: 20px;
      line-height: 1.2;
      @include xl{
        font-size: 20px;
      }
      @include sm{
        font-size: 18px;
      }
    }
  }
}