.our-services{
  padding-top: 98px;
  padding-bottom: 38px;
  @include sm{
    padding-top: 69px;
    &.custom{
      //padding-bottom: 168px;
    }
  }
  .section-header{
    @include sm{
      margin-bottom: 40px;
    }
    h2{
      @include sm{
        font-size: 30px;
      }
      span{
        font-weight: 500;
        text-transform: none;
        @include sm{
          font-size: 20px;
          padding: 0 20px;
        }
      }
    }
  }
  &-box{
    margin-bottom: 20px;
    height: calc(100% - 20px);
    border: 1px solid #E3E3E3;
    text-align: center;
    padding: 54px 30px 29px 30px;
    box-sizing: border-box;
    position: relative;
    &:hover{
      .our-services-box-hover{
        transition: all ease-in-out 0.2s;
        opacity: 1;
        visibility: visible;
      }
    }
    &-hover{
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      z-index: 3;
      background: linear-gradient(180deg, #B53131 0%, #F43434 100%);
      border: 1px solid #E3E3E3;
      left: -1px;
      top: -1px;
      color: #fff;
      height: calc(100% + 2px);
      width: calc(100% + 2px);
      padding: 20px;
      opacity: 0;
      visibility: hidden;
      transition: all ease-in-out 0.2s;
    }
    @include xxl{
      padding: 30px 20px;
    }
    @include sm{
      max-width: 203px;
      margin-left: auto;
      margin-right: auto;
      height: calc(100% - 15px);
      margin-bottom: 15px;
      padding: 48px 30px 50px 30px;
    }
    &-title{
      margin-bottom: 12px;
      font-weight: 600;
      font-size: 18px;
      line-height: 21px;
      text-transform: uppercase;
      color: #B53131;
      @include xxl{
        font-size: 16px;
      }
      @include sm{
        font-size: 18px;
      }
    }
    &-desc{
      @include xxl{
        font-size: 14px;
        line-height: 1.3;
      }
      @include sm{
        font-size: 16px;
      }
    }
  }
  &-footer{
    padding: 10px 0;
    align-items: center;
    justify-content: space-between;
    @include lg{
      flex-direction: column;
    }
    &-info{
      max-width: 615px;
      font-weight: 500;
      font-size: 20px;
      line-height: 23px;
      color: #1F1F1F;
      @include xl{
        font-size: 18px;
      }
      @include lg{
        margin-bottom: 26px;
        text-align: center;
      }
    }
    &-icon{}
  }
}