.header{
  padding: 21px 0;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 9;
  background-color: #fff;
  transition: all ease-in-out 0.2s;
  &.active{
    transition: all ease-in-out 0.2s;
    box-shadow: 4px 4px 8px 0px rgba(0, 0, 0, 0.05);
  }
  .logo,
  .header-toggle{
    @include xl{
      position: relative;
      z-index: 99;
    }
  }
  &-container{
    position: relative;
    justify-content: space-between;
  }
  &-actions{
    align-items: center;
    justify-content: flex-end;
    flex: 1;
    flex-wrap: nowrap;
    @include xl{
      position: fixed;
      width: 100vw;
      height: 100vh;
      overflow-y: auto;
      background-color: #fff;
      z-index: 3;
      left: 0;
      top: 0;
      flex-direction: column;
      justify-content: center;
      padding: 50px 0;

      opacity: 0;
      visibility: hidden;
      transition: all ease-in-out 0.2s;
      &.active{
        transition: all ease-in-out 0.2s;
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &-nav{
    flex: 1;
    @include xl{
      display: flex;
      align-items: center;
    }
    &-list{
      justify-content: center;
      @include xl{
        flex-direction: column;
        text-align: center;
      }
      li{
        &:not(:last-child){
          margin-right: 38px;
          @include xl{
            margin-right: 0;
            margin-bottom: 24px;
          }
        }
        a{
          font-size: 18px;
          color: #393939;
          position: relative;
          &:after{
            content: "";
            width: 0;
            height: 1px;
            background-color: var(--red);
            position: absolute;
            left: 0;
            bottom: 0;
            transition: all ease-in-out 0.2s;
          }
          &:hover{
            color: var(--red);
            &:after{
              transition: all ease-in-out 0.2s;
              width: 100%;
            }
          }
        }
      }
    }
  }
  &-toggle{
    display: none;
    width: 37px;
    height: 16px;
    position: relative;
    top: 6px;
    right: 8px;
    @include xl{
      display: block;
    }
    span{
      width: 100%;
      height: 2px;
      background-color: #191919;
      position: absolute;
      left: 0;
      transition: all ease-in-out 0.2s;
      &:nth-child(1){
        top: 0;
      }
      &:nth-child(2){
        bottom: 0;
      }
    }
    &.active{
      span{
        transition: all ease-in-out 0.2s;
        &:nth-child(1){
          top: 50%;
          transform: rotate(45deg) translate(0, -50%);
        }
        &:nth-child(2){
          bottom: inherit;
          top: 50%;
          transform: rotate(-45deg) translate(0, -50%);
        }
      }
    }
  }
  .socials{
    @include xl{
      width: 100%;
      justify-content: center;
      padding: 10px 0;
      border-top: 1px solid #707070;
      border-bottom: 1px solid #707070;
    }
  }
}