.button{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #fff;
  background: linear-gradient(89.74deg, #CA2F2F 3.02%, #EE3434 110.47%);
  font-weight: 600;
  font-size: 20px;
  line-height: 23px;
  padding: 16px 59px;
  transition: all ease-in-out 0.2s;
  @include sm{
    font-size: 16px;
    padding: 10px 35px;
    &.full-xs{
      max-width: 100%;
      width: 100%;
    }
  }
  &:hover,
  &:focus,
  &:active{
    outline: none;
  }
  &:hover{
    transition: all ease-in-out 0.2s;
    filter: drop-shadow(0px 10px 15px rgba(255, 43, 43, 0.49));
    color: #fff;
  }
  &:active{
    box-shadow: 0px 3px 13px 3px rgb(173, 29, 29) inset;
    filter: none;
  }
  &-arrow{
    width: 30px;
    height: 30px;
    background-image: url("../images/svg/white-button-arrow.svg");
    background-position: center;
    background-repeat: no-repeat;
    display: inline-block;
    margin-left: 10px;
  }
  &-white{
    color: #0F0F0F;
    background: #fff!important;
    &:hover{
      color: #0F0F0F;
      filter: none;
    }
    &:active{
      box-shadow: 0px 3px 13px 3px #bababa inset;
    }
    .button-arrow{
      background-image: url("../images/svg/black-button-arrow.svg");
    }
  }
}