.offer{
  padding-top: 80px;
  padding-bottom: 90px;
  text-align: center;
  position: relative;
  @include sm{
    padding-top: 55px;
    padding-bottom: 53px;
  }
  &:before{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-image: url("../images/radius-pattern-bg.png");
    background-position: center;
    z-index: 1;
  }
  .container{
    position: relative;
    z-index: 2;
  }
  h2{
    max-width: 681px;
    margin: 0 auto 34px auto;
    color: #fff;
    font-weight: 500;
    font-size: 40px;
    text-transform: uppercase;
    letter-spacing: -2px;
    @include lg{
      font-size: 32px;
    }
    @include md{
      font-size: 30px;
      line-height: 35px;
    }
  }
  @include sm{
    .button{
      padding: 18px 35px;
    }
  }
}