.form-group-title {
  margin-bottom: 8px;
  color: #F5F5F5;
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
}

.form-group-checkbox {
  margin-bottom: 15px;
}

.form-group-checkbox input[type="checkbox"] {
  display: none;
}

.form-group-checkbox input[type="checkbox"]:checked + span:after {
  transition: all ease-in-out 0.2s;
  opacity: 1;
}

.form-group-checkbox span {
  display: block;
  position: relative;
  padding-left: 28px;
  color: #F5F5F5;
  font-size: 15px;
}

.form-group-checkbox span:before, .form-group-checkbox span:after {
  content: "";
  position: absolute;
  cursor: pointer;
  transition: all ease-in-out 0.2s;
}

.form-group-checkbox span:before {
  width: 16px;
  height: 16px;
  border-radius: 5px;
  border: 2px solid #fff;
  left: 0;
  top: 3px;
}

.form-group-checkbox span:after {
  display: inline-block;
  left: 6px;
  top: 5px;
  transform: rotate(45deg);
  height: 9px;
  width: 5px;
  border-bottom: 2px solid;
  border-right: 2px solid;
  opacity: 0;
}

