.faq{
  padding-top: 129px;
  @include lg{
    padding-top: 70px;
  }
  &-inner{
    @include lg{
      flex-direction: column;
    }
  }
  &-questions{
    max-width: 553px;
    width: 100%;
    max-height: 373px;
    border: 1px solid #E3E3E3;
    overflow-y: scroll;
    margin-right: 30px;
    padding: 10px 0;
    @include xl{
      max-width: 40%;
    }
    @include lg{
      max-width: 100%;
      margin-bottom: 40px;
      max-height: 407px;
      margin-right: 0;
    }
    &::-webkit-scrollbar{
      width: 10px;
      background-color: #E9E9E9;
    }
    &::-webkit-scrollbar-thumb{
      background: linear-gradient(0deg, #DB3030, #DB3030);
    }
    &-item{
      cursor: pointer;
      font-weight: 500;
      font-size: 20px;
      line-height: 1.3;
      padding: 8px 22px;
      box-sizing: border-box;
      transition: all ease-in-out 0.2s;
      @include sm{
        padding: 0px 10px;
      }
      &.current{
        background: linear-gradient(110.89deg, #380D0D -65.39%, #EE3434 89.78%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
      }
    }
  }
  &-answers{
    flex: 1;
    &-item{
      &:not(.current){
        display: none;
      }
      &-header{
        margin-bottom: 26px;
        font-weight: 600;
        font-size: 40px;
        line-height: 47px;
        text-transform: uppercase;
        color: #0C0C0C;
        @include lg{
          font-size: 32px;
        }
        @include sm{
          font-size: 30px;
          line-height: 1.2;
          margin-bottom: 40px;
        }
      }
      &-content{}
      &-image{
        max-width: 204px;
        margin-right: 30px;
      }
      &-text{
        flex: 1;
        h4{
          font-weight: 600;
          font-size: 24px;
          line-height: 28px;
          text-transform: uppercase;
          margin-bottom: 22px;
        }
        p{
          font-size: 16px;
          line-height: 19px;
        }
      }
    }
  }
}