.how{
  padding-top: 90px;
  @include sm{
    padding-top: 63px;
  }
  &-inner{
    align-items: flex-end;
  }
  &-title{
    font-weight: 600;
    font-size: 40px;
    line-height: 47px;
    text-transform: uppercase;
    position: relative;
    padding-bottom: 24px;
    @include xxl{
      font-size: 32px;
    }
    @include xl{
      font-size: 28px;
      line-height: 1.3;
    }
    @include lg{
      margin-bottom: 50px;
    }
    @include sm{
      margin-bottom: 41px;
      padding-bottom: 0;
      max-width: 308px;
    }
    &:before{
      content: "";
      width: 60px;
      height: 60px;
      background-image: url("../images/svg/how-star.svg");
      background-position: center;
      background-repeat: no-repeat;
      left: -42px;
      top: -50px;
      position: absolute;
      @include sm{
        width: 30px;
        height: 30px;
        background-size: 28px;
        left: 163px;
        top: -15px;
      }
    }
  }
  &-items{}
  &-box{
    margin-bottom: 30px;
    @include sm{
      margin-bottom: 19px;
    }
    &-num{
      font-weight: 900;
      font-size: 40px;
      line-height: 47px;
      font-feature-settings: 'pnum' on, 'lnum' on;
      background: linear-gradient(110.89deg, #380D0D -65.39%, #EE3434 89.78%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      margin-bottom: 23px;
    }
    &-text{
      font-size: 16px;
      line-height: 19px;
      b{
        font-weight: 500;
        text-transform: uppercase;
      }
    }
  }
}