.work-gallery{
  background-image: url("../images/svg/work-gallery-bg.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  padding-top: 66px;
  padding-bottom: 94px;
  @include sm{
    padding-top: 58px;
    padding-bottom: 73px;
  }
  .section-header{
    margin-bottom: 45px;
    @include sm{
      margin-bottom: 51px;
    }
    h2{
      text-transform: uppercase;
      color: #fff;
      @include sm{
        padding: 0 40px;
        font-size: 30px;
      }
    }
  }
  &-slider{
    a{
      display: block;
      position: relative;
      padding-top: 60%;
      img{
        position: absolute;
        width: 100%;
        height: 100%;
        left: 50%;
        top: 50%;
        object-fit: cover;
        transform: translate(-50%, -50%);
      }
    }
    &.swiper-initialized{
      .swiper-slide{
        @include sm{
          padding: 0;
        }
      }
    }
  }
}