.best-exchange{
  padding-top: 153px;
  @include xl{
    padding-top: 100px;
  }
  @include md{
    padding-top: 60px;
  }
  &-box{
    text-align: center;
    border: 1px solid #B3B3B3;
    margin-bottom: 69px;
    @include lg{
      margin-bottom: 40px;
    }
    &-header{
      color: #B53131;
      font-weight: 600;
      font-size: 24px;
      line-height: 28px;
      padding: 12px 101px;
      border-bottom: 1px solid #B3B3B3;
      @include xl{
        font-size: 20px;
      }
      @include sm{
        font-size: 18px;
        line-height: 23px;
        padding: 10px 10px;
      }
    }
    &-footer{
      font-size: 20px;
      line-height: 23px;
      padding: 13px;
      @include xl{
        font-size: 18px;
      }
      @include sm{
        font-size: 16px;
        padding: 11px;
      }
    }
  }
  .button{
    @include sm{
      margin-top: 17px;
    }
  }
}