input[type="text"],
input[type="email"],
input[type="number"],
input[type="password"],
select,
textarea{
  max-width: 100%;
  width: 100%;
  border: none;
  background-color: #0A0A0A;
  color: #fff;
  min-height: 48px;
  padding: 0 20px;
  border-radius: 4px;
}
button.button{
  max-width: 100%;
  width: 100%;
  border: none;
}
.form-group{
  &-title{
    margin-bottom: 8px;
    color: #F5F5F5;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
  }
  &-checkbox{
    margin-bottom: 15px;
    input[type="checkbox"]{
      display: none;
      &:checked + span{
        &:after{
          transition: all ease-in-out 0.2s;
          opacity: 1;
        }
      }
    }
    span{
      display: block;
      position: relative;
      padding-left: 28px;
      color: #F5F5F5;
      font-size: 15px;
      &:before,
      &:after{
        content: "";
        position: absolute;
        cursor: pointer;
        transition: all ease-in-out 0.2s;
      }
      &:before{
        width: 16px;
        height: 16px;
        border-radius: 5px;
        border: 2px solid #fff;
        left: 0;
        top: 3px;
      }
      &:after{
        display: inline-block;
        left: 6px;
        top: 5px;
        transform: rotate(45deg);
        height: 9px;
        width: 5px;
        border-bottom: 2px solid;
        border-right: 2px solid;
        opacity: 0;
      }
    }
  }
}