.swiper{
  max-width: calc(100% - (160px * 2));
  margin: 0 auto;
  @include xxl{
    max-width: calc(100% - (60px * 2));
  }
  @include sm{
    max-width: 100%;
  }
  &-inner{
    position: relative;
  }
  &-controls{
    @include sm{
      margin-top: 20px;
      display: flex;
      justify-content: center;
    }
  }
  &-control{
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    z-index: 2;
    cursor: pointer;
    width: 40px;
    height: 40px;
    background-position: center;
    background-repeat: no-repeat;
    border: none;
    background-color: transparent;
    transition: all ease-in-out 0.2s;
    &:hover,
    &:focus,
    &:active{
      outline: none;
    }
    &:hover{
      opacity: 0.7;
    }
    &.prev{
      left: 70px;
      background-image: url("../images/svg/slider-prev.svg");
      @include xxl{
        left: 0;
      }
    }
    &.next{
      right: 70px;
      background-image: url("../images/svg/slider-next.svg");
      @include xxl{
        right: 0;
      }
    }
    @include sm{
      position: relative;
      background-size: 20px;
      top: 0;
      transform: none;
      &:not(:last-child){
        margin-right: 25px;
      }
    }
  }
  &-slide{

  }
}