.logo{
  font-weight: 700;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  span{
    color: var(--red);
  }
}


.socials{
  li{
    &:not(:last-child){
      margin-right: 12px;
    }
    a{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      &:hover{
        opacity: 0.8;
        path{
          transition: all ease-in-out 0.2s;
          stroke: #B53131;
        }
      }
      path{
        transition: all ease-in-out 0.2s;
      }
    }
  }
}


.red-color{
  background: linear-gradient(89.74deg, #CA2F2F 3.02%, #EE3434 110.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}


.top-scroll{
  position: fixed;
  z-index: 4;
  right: 30px;
  bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #C8C8C8;
  cursor: pointer;
  width: 64px;
  height: 64px;
  opacity: 0;
  visibility: hidden;
  transition: all ease-in-out 0.2s;
  @include sm{
    width: 30px;
    height: 30px;
    bottom: 15px;
    right: 15px;
    svg{
      max-height: 20px;
    }
  }
  &.active{
    transition: all ease-in-out 0.2s;
    visibility: visible;
    opacity: 1;
  }
  &:hover{
    transition: all ease-in-out 0.2s;
    background-color: #202020;
  }
}