.section{
  padding: var(--section-padding);
  overflow: hidden;
  &-pattern-bg{
    background-image: url("../images/radius-pattern-bg.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  &-black-bg{
    background-color: #202020;
  }
  &-red-bg{
    background: radial-gradient(53.93% 1778.15% at 56.74% 41.94%, #FF3D3D 0%, #B91F1F 100%);
  }
  &-black-bg,
  &-red-bg{
    color: #fff;
  }
  &-header{
    text-align: center;
    margin-bottom: 69px;
    @include sm{
      margin-bottom: 56px;
    }
    &-title{
      font-size: 40px;
      line-height: 47px;
      font-weight: 600;
      letter-spacing: -2.5px;
      margin: 0;
      @include xl{
        font-size: 32px;
        line-height: 1.4;
      }
      @include lg{
        font-size: 26px;
      }
      @include sm{
        font-size: 24px;
        line-height: 1.2;
      }
      span{
        display: block;
      }
    }
  }
}