.hero{
  padding-top: calc(97px + 68px);
  @include xl{
    padding-top: calc(50px + 68px);
  }
  &-header{
    position: relative;
    h1{
      letter-spacing: -3.5px;
      text-transform: uppercase;
      font-weight: 600;
      font-size: 60px;
      line-height: 1.1;
      margin-bottom: 20px;
      @include xl{
        font-size: 46px;
      }
      @include sm{
        font-size: 36px;
        letter-spacing: -2px;
        margin-bottom: 45px;
      }
      span{
        display: block;
        font-weight: 500;
        font-size: 40px;
        text-transform: none;
        letter-spacing: -2.5px;
        @include xl{
          font-size: 32px;
          letter-spacing: -1px;
        }
        @include md{
          font-size: 26px;
        }
        @include sm{
          font-size: 23px;
          padding-right: 50px;
          margin-top: 18px;
        }
      }
    }
    &-arrow{
      position: absolute;
      right: 0;
      top: 0;
      background-image: url("../images/svg/hero-arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-color: #202020;
      width: 101px;
      height: 101px;
      @include xl{
        width: 60px;
        height: 60px;
        background-size: 60%;
      }
      @include md{
        width: 40px;
        height: 40px;
        top: inherit;
        bottom: 0;
      }
      &:hover{
        filter: drop-shadow(0px 10px 15px rgba(28, 28, 28, 0.49));
      }
      &:active{
        filter: none;
        box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
      }
    }
  }
  &-content{
    position: relative;
    text-align: center;
    @include md{
      text-align: left;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &-desc{
      position: absolute;
      left: 0;
      top: 0;
      font-size: 20px;
      @include xl{
        font-size: 16px;
        line-height: 1.2;
      }
      @include md{
        position: relative;
        left: 0!important;
        top: 0!important;
        text-align: center!important;
        display: inline-block;
      }
      @include sm{
        font-size: 14px;
      }
      span{
        display: block;
        font-weight: 700;
      }
      &:before{
        content: "";
        width: 38px;
        height: 38px;
        background-size: 26px;
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        top: 50%;
        transform: translate(0,-50%);
        @include sm{
          background-size: 20px;
        }
      }
      &.desc-1{
        text-align: right;
        padding-right: 53px;
        left: 9%;
        top: 47%;
        &:before{
          right: 0;
          background-image: url("../images/svg/slider-next.svg");
        }
        @include xl{
          left: 4%;
        }
        @include md{
          padding-right: 0;
          padding-bottom: 50px;
          margin-left: 30px;
          &:before{
            right: inherit;
            left: 50%;
            top: initial;
            bottom: -8px;
            transform: translate(-50%, 0) rotate(90deg);
          }
        }
        @include sm{
          margin-left: 24px;
          padding-bottom: 35px;
        }
      }
      &.desc-2{
        padding-left: 53px;
        text-align: left;
        left: 77%;
        top: 72%;
        &:before{
          left: 0;
          background-image: url("../images/svg/slider-prev.svg");
        }
        @include xl{
          top: 61%;
        }
        @include md{
          padding-left: 0;
          padding-top: 31px;
          margin-left: auto;
          margin-right: 10px;
          &:before{
            left: 50%;
            top: -8px;
            transform: translate(-50%, 0) rotate(90deg);
          }
        }
      }
    }
    img{
      max-width: 775px;
      @include xxl{
        max-width: 605px;
      }
      @include xl{
        max-width: 50%;
      }
      @include md{
        display: block;
        max-width: 100%;
      }
    }
  }
}