.steps{
  padding-top: 87px;
  padding-bottom: 99px;
  @include lg{
    padding: 60px 0;
  }
  @include sm{
    padding-top: 70px;
    padding-bottom: 56px;
  }
  &-inner{
    border: 1px solid #E3E3E3;
    padding: 40px;
    box-sizing: border-box;
    @include sm{
      padding: 0;
      border: none;
    }
  }
  &-items{}
  &-box{
    display: flex;
    align-items: center;
    margin-bottom: 37px;
    @include sm{
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 70px;
    }
    &-num{
      font-weight: 900;
      font-size: 80px;
      min-width: 120px;
      line-height: 1;
      @include sm{
        font-size: 40px;
        margin-bottom: 25px;
      }
    }
    &-text{
      flex: 1;
      font-size: 18px;
      line-height: 21px;
      span{
        font-weight: 500;
      }
      @include sm{
        font-size: 18px;
      }
    }
  }
  &-footer{
    text-align: center;
    margin-top: -12px;
    @include sm{
      width: 100vw;
      position: relative;
      left: 50%;
      transform: translate(-50%, 0);
      margin-top: -23px;
    }
    &-text{
      max-width: 770px;
      display: inline-block;
      color: #fff;
      padding: 38px 20px;
      font-weight: 500;
      font-size: 20px;
      line-height: 23px;
      box-sizing: border-box;
      background-color: #363636;
      @include sm{
        font-size: 18px;
        line-height: 23px;
        text-align: left;
      }
    }
  }
}